import React from 'react';
import SelectorButton, { SelectorButtonProps } from './SelectorButton';

export interface SelectorOption {
  /**   */
  id: string;
  /**    */
  label: string;
  /**    */
  disabled?: boolean;
}

export interface SelectorGroupProps {
  /**     */
  options: SelectorOption[];
  /**   */
  selectedValue?: string;
  /**    */
  onChange?: (value: string) => void;
  /**   */
  size?: SelectorButtonProps['size'];
  /**   */
  variant?: SelectorButtonProps['variant'];
  /**  CSS    */
  className?: string;
  /**   */
  title?: string;
  /**     */
  allowDeselect?: boolean;
}

const SelectorGroup: React.FC<SelectorGroupProps> = ({
  options,
  selectedValue,
  onChange,
  size = 'md',
  variant = 'default',
  className = '',
  title,
  allowDeselect = false
}) => {
  const handleOptionClick = (optionId: string) => {
    if (!onChange) return;

    //  allowDeselect = true      ,  
    if (allowDeselect && selectedValue === optionId) {
      onChange('');
    } else {
      onChange(optionId);
    }
  };

  return (
    <div className={`space-y-2 sm:space-y-2.5 ${className}`}>
      {title && (
        <p className="text-white/60 font-manrope font-medium text-xs sm:text-sm mb-1.5">
          {title}
        </p>
      )}
      <div className="flex flex-wrap gap-2 sm:gap-2.5 md:gap-3">
        {options.map((option) => (
          <SelectorButton
            key={option.id}
            label={option.label}
            selected={selectedValue === option.id}
            onClick={() => handleOptionClick(option.id)}
            disabled={option.disabled}
            size={size}
            variant={variant}
          />
        ))}
      </div>
    </div>
  );
};

export default SelectorGroup;